*{
  margin: 0;
  padding:0;

}
.header{
  min-height: 100vh;
  width: 100%;
  background: rgba(0, 0, 0 , 0.7) url(./public/images/bg1.jpg);
  /* background-color: #ccc; */
  background-size: cover;
  background-blend-mode: darken;
  position: relative;
  background-position: center;
}
.navbar{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav-links{
  flex: 1;
  width: 70%;
  text-align: right;

}
.navLogo{
  width: 20%;
}
.navLogo img{
  height: 60px;
  width: 123px;
  margin: 0px 23px;
}
.navlist li{
  list-style: none;
  text-decoration: none;
  padding: 0px 12px;
  display: inline-block;
  position: relative;
  text-align: right;
  padding-bottom: 5px;
}
.navlist li a{
  list-style: none;
  text-decoration: none;
  padding: 0px 5px;
  display: inline-block;
  position: relative;
  text-align: right;
  color: white;
  font-size: 15px;
  font-family: 'Poppins', sans-serif;
  margin: auto;

}
.navlist li ::after{
  content: '';
  width: 0%;
  height: 2px;
  display: block;
  margin: auto;
  transition: 0.7s;
  background: #f44336;
   
}
.navlist li:hover ::after{
  width: 100%;
}
.textBox{
  width: 90%;
  color: white;
  font-family: 'Poppins', sans-serif;
  position:absolute;
  top: 50%;
  left: 50%;
  /* text-align: center; */
  transform: translate(-50%, -50%);

}
.textBox h1{
  font-size: 1.2rem;
}

.textBox .bigFont {
  font-size: 4.2rem;
}

.textBox p{
  font-size: 1.2rem;
  margin: 10px 0 14px;
}
.hero-btn{
  border: 1px solid white;
  background: transparent;
  color: white;
  text-decoration: none;
  padding: 8px 9px;
  cursor: pointer;
  position: relative;
  display: inline-block;
  margin-left: 10px;
}
.hero-btn:hover{
  border: 1px solid brown;
  background-color: #f44336;
  transition: 0.7s;
  border-radius: 2px;

}
nav .fa{
  display: none;
  
}
/* .fa-bars{
  position: absolute;
  right: 200px;
 
  z-index: 2;
} */
.burger{
  display: none;
  position: absolute;
  top: 15px;
  right: 5%;
  cursor: pointer;
}
.line{
  height: 3px;
  width: 20px;
  background-color: white;
  color: white;
  margin: 3px 3px;
}
@media(max-width: 950px){
  .textBox h1{
      font-size: 2.2rem;
  }
  .navlist li{
      display: block;
      text-align: left;
      padding: 10px 0px;

  
  }
  .nav-links{
      position: fixed;
      background: #f44336;
      height: 100vh;
      width: 200px;
      top: 0;
      right: -200px;
      z-index: 2;
  
      
  }
  .nav-links .fa{
      display: inline-block;
      color: white;
      margin: 10px;
      font-size: 20px;
      cursor: pointer;
      text-align: left;
      margin-bottom: 30px;
  }
  .navlist{
      padding: 30px;
      margin-top: 30px;
  }
  .burger{
      display: block;
  }
}
@media(max-width: 500px){
  .textBox h1{
      font-size: 1.2rem;
  }
  .textBox p{
      font-size: 15px;
  }
  .hero-btn{
      border: 1px solid white;
      background: transparent;
      color: white;
      text-decoration: none;
      padding: 2px 5px;
      cursor: pointer;
      position: relative;
      display: inline-block;
      
      
  }
  .hero-btn:hover{
      border: 1px solid brown;
      background-color: #f44336;
      transition: 0.7s;
  
  }
  .navlist li{
      display: block;
      text-align: left;

  }
  .nav-links{
      position: fixed;
      background: #f44336;
      height: 100vh;
      width: 200px;
      top: 0;
      right: 0;
      text-align: left;
      z-index: 2;
  }
}
/* --------Course section------ */
.course{
  width: 80%;
  margin: auto;
  text-align: center;
  padding-top: 100px;
  margin-bottom: 100px;
}
h1{
  font-size: 2.2rem;
  font-weight: 600;
  font-family: 'Poppins', sans-serif;
}
.para{
  font-size: 1.1rem;
  color: #777;
  
  font-weight: 300;
  padding: 10px;
}
.row{
  display: flex;
  justify-content: space-between;
  margin-top: 5%;

}
.course-column{
  background: #fff3f3;
  box-sizing: border-box;
  border-radius: 10px;
  padding: 20px 12px;
  flex-basis: 31%;
  margin-bottom: 10px;
  transition: 0.5s;
}
.course-column h3{
  text-align: center;
  font-weight: 900;
  margin: 7px 0px;
}
.course-column:hover{
  box-shadow: 0px 0 20px 0 rgba(0, 0, 0 , 0.2);
}
@media(max-width: 850px){
  .row{
      flex-direction: column;
  }
  .testimonial-column img{
      
      margin-left: 0px;
      margin-right: 5px;
      
  }
}

/* -----Campus Section----- */
.campus{
  text-align: center;
  margin: auto;
  padding-top: 30px;
  width: 80%;
}
.campus h1{
  text-align: center;
  font-size: 2.2rem;
}
.campus-column{
  flex-basis: 32%;
  border-radius: 10px;
  margin-bottom: 30px;
  position: relative;
  overflow: hidden;
}
.campus-column img{
  width: 100%;
  display: block;
}
.layer{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  transition: 0.7s;
  background: transparent;
}
.layer:hover{
  background: rgba(277, 0, 0 , 0.2);
  
}
.layer h3{
  transform: translateX(-50%);
  position: absolute;
  left: 50%;
  color: white;
  bottom: 0;
  font-size: 26px;
  font-weight: 500;
  width: 100%;
  opacity: 0;
  transition: 0.7s;
}
.layer:hover h3{
  opacity: 1;
  bottom: 3%;
}

/* ------Facilities Section---- */
.facilities{
  text-align: center;
  margin: auto;
  width: 80%;
  padding-top: 30px;
  margin-bottom: 20px;
}
.facilities-column{
  flex-basis: 31%;
  border-radius: 10px;
  margin-bottom: 10px;
  text-align: left;
}
.facilities-column img{
  width: 100%;
  border-radius: 10px;
  margin-bottom: 20px;
  max-height: 262px;
}  
.facilities-column h3{
  text-align: left;
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: 'Poppins', sans-serif;
}
.facilities-column p{
  padding: 0px;
}

/* -------testimonials------ */
.testimonial{
  width: 80%;
  margin: auto;
  padding-top: 100px;
  text-align: center;
}
.testimonial-column{
  flex-basis: 44%;
  border-radius: 10px;
  margin-bottom: 5%;
  text-align: left;
  display: flex;
  padding: 25px;
  background: #fff3f3;
  cursor: pointer;
  transition: 0.7s;
}
.testimonial-column img{
  height: 40px;
  margin-left: 5px;
  margin-right: 30px;
  border-radius: 50%;
}
.testimonial-column:hover{
  box-shadow: 0 0 20px 0 rgba(0, 0, 0 , 0.2);
  
}
.testimonial-column h3{
  margin-bottom: 5px;
  margin-top: 10px;
}
.testimonial-column p{
  padding: 0;
}
.testimonial-column .fa{
  color: #f44336;
}
.fa-times{
  position: absolute;
  left: 0;
  margin-bottom: 30px;
}


/* ------Call to Action Section---- */
.cta{
  margin: 10px auto;
  width: 80%;
  background-image: linear-gradient(rgba(0 , 0 , 0, 0.7) , rgba(0 , 0 , 0, 0.7)), url(./public/images/banner2.jpg);
  /* background-image: linear-gradient(rgba(0 , 0 , 0, 0.7) , rgba(0 , 0 , 0, 0.7)), url(images/banner2.jpg); */
  background-size: cover;
  background-position: center;
  padding:10px 0px;
  text-align: center;
  border-radius: 10px;


}
.cta h1{
  color: white;
  margin: 40px;
  font-family: 'Poppins', sans-serif;
  padding: 0;
}
@media(max-width: 850px){
  .cta h1{
      font-size: 24px;
  }
}


/* ---------Footer Section------ */
.footer{
  width: 100%;
  text-align: center;
  padding: 30px 0px;
}
.footer h4{
  margin-bottom: 10px;
  margin-top: 20px;
  font-weight: 600;
}
.icons .fa{
  color: #f44336;
  font-size: 24px;
  margin: 0px 5px;
  padding: 10px 0px;
}
.fa-heart-o{
  color: #f44336;
}


/* ---------About Us------- */
.sub-header{
  height: 50vh;
  width: 100%;
  background: linear-gradient(rgba(4, 9 ,30, 0.7), rgba(4, 9 ,30, 0.7)), url(./public/images/bg1.jpg);
  background-position: center;
  background-size: cover;
  text-align: center;
  color: white;
  margin-bottom: 30px;
}
.sub-header h1{
  margin-top: 100px;
  font-family: 'Poppins', sans-serif;
}
.about-us{
  width: 80%;
  margin: auto;
  padding-top: 30px;
  padding-bottom: 25px;
}
.about-column{
  flex-basis: 44%;
  padding: 10px 20px;
}
.about-column img{
  width: 100%;
  border-radius: 10px;
}
.about-column h1{
  padding: 0px;
}
.about-column p{
  padding: 15px 0 25px;
}
.red-btn{
  border: 1px solid #f44336;
  color: #f44336;
  background: transparent;

}
.red-btn:hover{
  color: white;
  transition: 0.7s;
}



/* -------Blog Content----- */
.blog{
  width: 80%;
  margin: auto;
  padding: 30px 0;
}
.blog-left{
  flex-basis: 65%;
}
.blog-left img{
  border-radius: 10px;
  width: 80%;
}
.blog-left h2{
  font-weight: 600;
  margin: 30px 0;
  color: #222;
}
.blog-left p{
  padding: 0;
  color: #999;
  width: 80%;
}

.blog-right{
  flex-basis: 32%;
}
.blog-right h3{
  background: #f44336;
  color: white;
  padding: 7px 0;
  font-size: 16px;
  margin-bottom: 20px;
  text-align: center;
}
.blog-right div{
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 10px;
}

.comment-form{
  border: 1px solid #ccc;
  margin: 50px 0;
  padding: 10px 20px;
  width: 80%;
}
.comment-form h3{
  text-align: left;
  font-family: 'Poppins', sans-serif;
}
.comment-form input, .comment-form textarea{
  width: 100%;
  padding: 10px 10px;
  margin: 15px 0px;
  border: none;
  background: #f0f0f0;
  box-sizing: border-box;
  outline: none;
  border-radius: 5px;
  font-family: 'Poppins', sans-serif;
}
.comment-form button{
  padding: 8px 12px;
}
@media(max-width: 850px){
  .sub-header h1{
      margin-top: 80px;
      font-family: 'Poppins', sans-serif;
      font-size: 1.5rem;
  }

}
