.scrolling-words-container {
  display: flex;
  align-items: center;
  font-size: 2rem;
  font-weight: 600;
  
}

.scrolling-words-box {
  height: 2rem;
  overflow: hidden;
}
.scrolling-words-box ul {
  margin: 0 0.625rem;
  padding: 0;
  animation: scrollUp 4s infinite;
}
.scrolling-words-box ul li {
  display: flex;
  align-items: center;
  height: 2rem;
  list-style: none;
}

@keyframes scrollUp {
  15%, 25% {
    transform: translateY(-30%);
  }
  40%, 50% {
    transform: translateY(-30%);
  }
  65%, 75% {
    transform: translateY(-40%);
  }
  90%, 100% {
    transform: translateY(-60%);
  }
}

.about-para {
  line-height: 2.2rem;
}

.footer-para {
  width: 60%;
  margin: auto;
  line-height: 1.3rem;
}

.error {
  margin-bottom: 5px !important;
  color: red;
}

